<template>
  <div class="compbox">
    <mydrag
      :active="getThisComp.active"
      :enable="getThisComp.draggable"
      :handles="'ne, se, sw, nw'"
      :top="getThisComp.position.top"
      :left="getThisComp.position.left"
      :width="getThisComp.position.width"
      :height="getThisComp.position.height"
      :transform="getThisComp.position.transform"
    >
      <div class="inner">
        <img :src="icon" height="100%">
        <audio :src="currMusic" autoplay @canplay="onPlayReady" @ended="onPlayEnded" ref="audioPlayer"></audio>
      </div>
    </mydrag>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import mydrag from '@/plugin/mydrag'

  export default {
    components: {
      mydrag
    },
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data: function () {
      return {
        playindex:0,
        playsrc:'',
        icon:require('@/assets/music.png')
      }
    },
    mounted(){
    },
    computed: {
      ...mapGetters([
        'getCompById'
      ]),
      getThisComp:function(){
        return this.getCompById(this.compid)
      },
      animateStyle: function () {
        return {
          animationName: this.getThisComp.name,
          animationDuration: this.getThisComp.duration + 's',
          animationDelay: this.getThisComp.delay + 's'
        };
      },
      getPlayer() {
        return this.$refs.audioPlayer
      },
      currMusic() {
        return this.getThisComp.musiclist[this.playindex].src
      }
    },
    methods:{
      onPlayReady(){
        /*wkshop.log(this.getThisComp.musiclist)
        if(this.getThisComp.musiclist==1){
          this.getPlayer.loop=true
        }else{
          this.getPlayer.loop=false
        }*/
      },
      onPlayEnded(){

        if(this.getThisComp.musiclist.length>1) {

          if (this.playindex < (this.getThisComp.musiclist.length - 1)) {
            this.playindex = this.playindex + 1
          } else {
            this.playindex = 0
          }
        }
        else{
          this.getPlayer.play()
        }
      }
    }
  }
</script>
<style scoped>
  .compbox {
    position: absolute;
    top: 0;
    left: 0;
  }

  .inner {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



