<template>
  <div class="compbox">
    <mydrag
      :active="getThisComp.active"
      :enable="getThisComp.draggable"
      :handles="'ne, se, sw, nw'"
      :top="getThisComp.position.top"
      :left="getThisComp.position.left"
      :width="getThisComp.position.width"
      :height="getThisComp.position.height"
      :transform="getThisComp.position.transform"
    >
      <div class="marquee" :style="[getThisComp.style, animateStyle]" >
        {{getThisComp.text}}
      </div>
    </mydrag>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import mydrag from '@/plugin/mydrag'
  import 'jquery.marquee/jquery.marquee.min.js'

  export default {
    components: {
      mydrag
    },
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data: function () {
      return {
      }
    },
    mounted(){
      $('.marquee').marquee({
        //duration in milliseconds of the marquee
        duration: 15000,
        //gap in pixels between the tickers
        gap: 100,
        //time in milliseconds before the marquee will start animating
        delayBeforeStart: 0,
        //'left' or 'right'
        direction: 'left',
        //true or false - should the marquee be duplicated to show an effect of continues flow
        duplicated: true
      });
    },
    computed: {
      ...mapGetters([
        'getCompById'
      ]),
      getThisComp:function(){
        return this.getCompById(this.compid)
      },
      animateStyle: function () {
        return {
          animationName: this.getThisComp.name,
          animationDuration: this.getThisComp.duration + 's',
          animationDelay: this.getThisComp.delay + 's'
        };
      }
    },
  }
</script>
<style scoped>
  .compbox {
    position: absolute;
    top: 0;
    left: 0;
  }

  .marquee {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



