<template>
  <el-tabs type="border-card">
    <el-tab-pane label="样式" class="wraptab">
      <el-form label-width="80px">
        <el-form-item label="页面名称:">
          <el-input v-model="name" @change="changeName" size="small"></el-input>
        </el-form-item>
        <el-form-item label="页面宽度:">
          <el-input-number v-model="width" size="small" controls-position="right" @change="changeWidth" :min="100" :max="10000"></el-input-number>
        </el-form-item>
        <el-form-item label="页面高度:">
          <el-input-number v-model="height" size="small" controls-position="right" @change="changeHeight" :min="100" :max="10000"></el-input-number>
        </el-form-item>
        <el-form-item label="缩放比例:">
          <el-slider v-model="zoom" :min="10" :max="100" show-input input-size="small"></el-slider>
        </el-form-item>
        <el-form-item label="背景颜色:">
          <el-color-picker v-model="bgcolor" size="small"></el-color-picker>
        </el-form-item>
        <el-form-item label="背景图片:">
          <el-button  type="text" @click="changeBgImg">更换图片</el-button>
          <el-button  type="text" @click="deleteBgImg">删除图片</el-button>
        </el-form-item>
        <el-form-item label="页面标签:">
          <el-input v-model="tags" @change="changeTags" size="small"></el-input>
        </el-form-item>
        <el-form-item label="页面描述:">
          <el-input type="textarea" v-model="remarks" @change="changeRemarks" size="small"></el-input>
        </el-form-item>
      </el-form>
    </el-tab-pane>
  </el-tabs>
</template>
<script>

  import { mapGetters } from 'vuex'
  import matedlg from '@/frames/matedlg'

  export default {
    data() {
      return {
        name: '我的页面',
        width: 1920,
        height: 1080,
        zoom: 50,
        bgcolor: '#FFFFFF',
        tags: '',
        remarks: ''
      }
    },
    mounted(){

      this.name=this.getCurrentPage.name
      this.width=this.getCurrentPage.width
      this.height=this.getCurrentPage.height
      this.zoom=this.getCurrentPage.zoom*100
      this.bgcolor=this.getCurrentPage.bgColor
      this.tags=this.getCurrentPage.tags
      this.remarks=this.getCurrentPage.remarks
    },
    methods: {
      changeName(value){
        this.getCurrentPage.name=value
      },
      changeWidth(value) {
        this.getCurrentPage.width=value
      },
      changeHeight(value){
        this.getCurrentPage.height=value
      },
      changeBgImg(){

        let that=this
        this.$dlg.modal(matedlg, {
          title:'选择素材',
          width: 1050,
          height: 650,
          params: {},
          callback: data => {
            if(data!=null) {
              this.getCurrentPage.bgImage = data.itemList[0].src
            }
          }
        })
      },
      deleteBgImg(){
        this.getCurrentPage.bgImage=''
      },
      changeTags(value){
        this.getCurrentPage.tags=value
      },
      changeRemarks(value){
        this.getCurrentPage.remarks=value
      },
    },
    watch: {
      zoom: function (newVal, oldVal) {
        this.getCurrentPage.zoom=newVal/100
      },
      bgcolor: function (newVal, oldVal) {
        this.getCurrentPage.bgColor=newVal
      }
    },
    computed: {
      ...mapGetters([
        'getCurrentPage'
      ]),
    }
  };
</script>
<style scoped>
  .wraptab {
    margin: 0;
    padding: 0;
    min-height: 400px;
  }
</style>



