<template>
  <el-tabs type="border-card">
    <el-tab-pane label="样式" class="wraptab">
      <el-form  label-width="50px">
        <el-row>
          <el-col :span="24">
            <el-form-item label="文字:">
              <el-input type="textarea" v-model="getCurrentComp.text" size="small"></el-input>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="方向:">
              <el-select v-model="getCurrentComp.direction" placeholder="请选择" size="small">
                <el-option
                  v-for="item in directList"
                  :key="item.value"
                  :label="item.name"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="颜色:" size="small">
              <el-color-picker v-model="getCurrentComp.style.color" size="small"></el-color-picker>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="背景:" size="small">
              <el-color-picker v-model="getCurrentComp.style.backgroundColor" size="small"></el-color-picker>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="样式:">
              <el-button-group>
                <el-button size="small" @click="onTxtBoldit">B</el-button>
                <el-button size="small" @click="onTxtItalic">I</el-button>
                <el-button size="small" @click="onUnderline">U</el-button>
              </el-button-group>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="字号:">
              <el-select v-model="getCurrentComp.style.fontSize" placeholder="请选择" size="small">
                <el-option
                  v-for="item in getFontsizeList"
                  :key="item"
                  :label="item"
                  :value="item">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="字体:">
              <el-select v-model="getCurrentComp.style.fontFamily" placeholder="请选择" size="small">
                <el-option
                  v-for="item in getFtfamilyList"
                  :key="item.id"
                  :label="item.name"
                  :value="item.value">
                </el-option>
              </el-select>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="行高:">
              <el-slider v-model="getCurrentComp.style.lineHeight" :min="1" :max="10" show-input input-size="small"></el-slider>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-form-item label="旋转:">
              <el-slider v-model="getCurrentComp.position.transform" :min="0" :max="360" show-input input-size="small"></el-slider>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="上边:" >
              <el-input-number v-model="getCurrentComp.position.top" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="左边:" >
              <el-input-number v-model="getCurrentComp.position.left" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="宽度:">
              <el-input-number v-model="getCurrentComp.position.width" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="高度:">
              <el-input-number v-model="getCurrentComp.position.height" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="动画" class="wraptab">
      <el-form label-width="50px">
        <el-form-item label="类型:">
          <el-select v-model="getCurrentComp.animation.name" placeholder="请选择">
            <el-option-group
              v-for="group in getAnimtypeList"
              :key="group.label"
              :label="group.label">
              <el-option
                v-for="item in group.options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-option-group>
          </el-select>
        </el-form-item>
        <el-form-item label="速度:">
          <el-slider v-model="getCurrentComp.animation.duration" :min="1" :max="100" show-input input-size="small"></el-slider>
        </el-form-item>
        <el-form-item label="延迟:">
          <el-slider v-model="getCurrentComp.animation.delay" :min="0" :max="100" show-input input-size="small"></el-slider>
        </el-form-item>
      </el-form>
    </el-tab-pane>
  </el-tabs>
</template>
<script>

  import { mapGetters } from 'vuex'

  export default {
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data() {
      return {
        directList:[
          {name:"向左",value:"left"},
          {name:"向右",value:"right"},
          {name:"向上",value:"up"},
          {name:"向下",value:"down"}]
      }
    },
    computed: {
      ...mapGetters([
        'getCurrentComp',
        'getFontsizeList',
        'getFtfamilyList',
        'getAnimtypeList'
      ]),
    },
    methods: {
      onTxtBoldit() {
        if(this.getCurrentComp.style.fontWeight==='bold'){
          this.getCurrentComp.style.fontWeight = "normal"
        }else {
          this.getCurrentComp.style.fontWeight = "bold"
        }
      },
      onTxtItalic() {
        if(this.getCurrentComp.style.fontStyle==='italic'){
          this.getCurrentComp.style.fontStyle = "normal"
        }else {
          this.getCurrentComp.style.fontStyle = "italic"
        }
      },
      onUnderline() {
        if(this.getCurrentComp.style.textDecoration==='underline'){
          this.getCurrentComp.style.textDecoration = "none"
        }else {
          this.getCurrentComp.style.textDecoration = "underline"
        }
      }
    }
  };
</script>
<style scoped>
  .wraptab {
    margin: 0;
    padding: 0;
    min-height: 400px;
  }
</style>



