<template>
  <div class="workspace" :style="style" >
    <component v-for="comp in getPageComps" v-bind:key="comp.compid"
               :compid="comp.compid" :is="comp.type+ 'view'"
               v-on:click.native.stop="activeComp(comp.compid)"
               v-on:mousedown.native.stop="activeComp(comp.compid)"
    ></component>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import { mapActions } from 'vuex'

  import canvasview from '@/comps/canvasview'
  import textview from '@/comps/texteview'
  import marquview from '@/comps/marquview'
  import timeview from '@/comps/timerview'
  import videoview from '@/comps/videoview'
  import imageview from '@/comps/imageview'
  import musicview from '@/comps/musicview'

  export default {
    components: {
      canvasview,
      textview,
      marquview,
      timeview,
      videoview,
      imageview,
      musicview
    },
    data() {
      return {
      }
    },
    mounted () {
    },
    methods:{
      ...mapActions([
        'setActivePage',
        'setActiveComp',
        'setResetComps'
      ]),
      activeComp: function (compid) {
        this.setResetComps()
        this.setActiveComp(compid)
      }
    },
    computed: {
      ...mapGetters([
        'getCurrentPage','getAllComps'
      ]),
      getThisPage:function(){
        return this.getCurrentPage
      },
      getPageComps:function(){
        return this.getAllComps
      },
      style: function () {

        return {
          width: (this.getThisPage.width*this.getThisPage.zoom)+'px',
          height: (this.getThisPage.height*this.getThisPage.zoom)+'px',
          backgroundColor:this.getThisPage.bgColor,
          backgroundImage:'url('+this.getThisPage.bgImage+')'
        }
      }
    },
  };
</script>
<style scoped>
  .workspace {
    margin: 0;
    padding: 0;
    background-color: white;
    position: relative;
  }
</style>



