import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/pages/index'
import Preview from '@/pages/preview'
import Debug from '@/pages/debug'

Vue.use(Router)

export default new Router({
  base: '/DesignWeb/',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Index',
      component: Index
    },
    {
      path: '/Preview',
      name: 'Preview',
      component: Preview
    },
    {
      path: '/Debug',
      name: 'Debug',
      component: Debug
    }
  ]
})



