<template>
  <div class="wrapbox">
    <pre><code>{{getCurrentSlide}}</code></pre>
  </div>
</template>

<script>

  import { mapGetters } from 'vuex'

  export default {
    data() {
      return {}
    },
    computed: {
      ...mapGetters([
        'getCurrentSlide'
      ]),
    }
  }
</script>

<style>
  .wrapbox {
    margin: 0;
    padding: 0;
    width: 100%;
    heigth:100%;
  }

</style>



