<template>
  <div class="propsbox">
    <component :compid="getThisComp.compid" :is="getThisComp.type+ 'prop'"></component>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import { mapActions } from 'vuex'

  import canvasprop from '@/comps/canvasprop'
  import textprop from '@/comps/texteprop'
  import marquprop from '@/comps/marquprop'
  import timeprop from '@/comps/timerprop'
  import videoprop from '@/comps/videoprop'
  import imageprop from '@/comps/imageprop'
  import musicprop from '@/comps/musicprop'

  export default {
    components: {
      canvasprop,
      textprop,
      marquprop,
      timeprop,
      videoprop,
      imageprop,
      musicprop
    },
    data() {
      return {
      }
    },
    computed: {
      ...mapGetters([
       'getAllComps','getCurrentCompId'
      ]),
      getThisComp:function(){
        return this.getAllComps.find(item => item.compid === this.getCurrentCompId)
      },
    }
  };
</script>
<style scoped>
  .propsbox {
    margin: 0;
    padding: 0;
    min-height: 400px;
  }
</style>



