// 实时监听state值的变化(最新状态)
const getters = {
  getAppCode(state) {
    return state.appCode
  },
  getHomePageId(state) {
    return state.homePageId
  },
  getHomePage(state) {
    return state.slide.pages.find(item=>item.pageid=state.homePageId)
  },
  getCurrentSlideId(state) {
    return state.currentSlideId
  },
  getCurrentSlide(state) {
    return state.slide
  },
  getCurrentPageId(state) {
    return state.currentPageId
  },
  getCurrentPage(state) {
    return state.slide.pages.find(item=>item.pageid=state.currentPageId)
  },
  getCurrentCompId(state) {
    return state.currentCompId
  },
  getCurrentComp(state) {
    return state.slide.pages.find(item=>item.pageid=state.currentPageId).comps.find(item=>item.compid===state.currentCompId)
  },
  getAllPages(state){
    return state.slide.pages;
  },
  getPagesByCondition(state,condition){},
  getPageById: (state) => (pageid) => {
    return state.slide.pages.find(item => item.pageid === pageid)
  },
  getAllComps(state){
    return state.slide.pages.find(item=>item.pageid===state.currentPageId).comps;},
  getCompsByCondition(state,condition){},
  getCompById: (state) => (compid) => {
    return state.slide.pages.find(item=>item.pageid===state.currentPageId).comps.find(item => item.compid === compid)
  },
  getFontsizeList(state){
    return state.fontSizeList
  },
  getFtfamilyList(state){
    return state.ftFamilyList
  },
  getAnimtypeList(state){
    return state.animTypeList
  }
}
const mutations = {
  putAppCode(state, appCode) {
    state.appCode = appCode
  },
  putMySlide(state,slide){
     state.slide=slide
  },
  putActiveSlide(state, slideid) {
      state.currentSlideId=slideid
  },
  putActivePage(state, pageid) {
    let page=state.slide.pages.find(item => item.pageid === pageid)
    if(page!=null) {
      state.currentPageId=pageid
    }
  },
  putActiveComp(state, compid) {
    let comp=state.slide.pages.find(item=>item.pageid===state.currentPageId).comps.find(item => item.compid === compid)
    if(comp!=null) {
      comp.active = true
      comp.draggable=true
      state.currentCompId=compid
    }
  },
  putResetComps(state) {
    state.slide.pages.find(item=>item.pageid===state.currentPageId).comps.map(function (item) {
      item.active = false;
      return item;
    });
  },
  putActiveCompSize(state,obj){
    let comp=state.slide.pages.find(item=>item.pageid===state.currentPageId).comps.find(item => item.compid === obj.compid)
    if(comp!=null) {
      comp.position.top = obj.top
      comp.position.left = obj.left
      comp.position.width = obj.width
      comp.position.height = obj.height
    }
  },
  putActiveCompPost(state,obj){
    let comp=state.slide.pages.find(item=>item.pageid===state.currentPageId).comps.find(item => item.compid === obj.compid)
    if(comp!=null) {
      comp.position.top = obj.top
      comp.position.left = obj.left
    }
  },
  addNewPage(state){

    let pages=state.slide.pages;
    let uuid=getUuid()//获取唯一编号
    let page={
      pageid: uuid,
      name:'我的页面',
      type: 'normal',
      tags:'',
      width: 600,
      height: 400,
      zoom:1,
      bgColor:'#ffffff',
      bgImage: '',
      comps: [
        {
          compid: 1,
          name:'默认画布',
          type: "canvas",
          active: false,
          draggable:false,
          remarks:"默认画布"
        }],
      remarks: "默认页面"
    };

    pages.push(page);
    state.currentPageId=uuid;
  },
  updatePage(state,obj){},
  deletePage(state,pageid){

    let page=state.slide.pages.find(item=>item.pageid===pageid);

    if(page!=null){
      let index = state.slide.pages.indexOf(page)
      state.slide.pages.splice(index, 1)
    }
  },
  addNewComp(state,compType){

    let comps=state.slide.pages.find(item=>item.pageid===state.currentPageId).comps;
    let comp=null;
    let uuid=getUuid()//获取唯一编号

    switch (compType) {
      case "text":
        comp={
          compid: uuid,
          name:'静态文本',
          type: "text",
          active: true,
          draggable:true,
          text: "请输入文本",
          duration:'10',
          position: {
            top: 144,
            left: 176,
            width: 200,
            height: 24,
            transform: 0
          },
          style: {
            color: "#333333",
            backgroundColor:'#ffffff',
            fontFamily: "Microsoft Yahei",
            fontSize: "16px",
            fontStyle: "normal",
            fontWeight: "normal",
            lineHeight: 1,
            textAlign: "center",
            textDecoration: "none",
          },
          animation:{
            name:'',
            duration:10,
            delay:1
          }
        }
        break;
      case "marqu":
        comp={
          compid:uuid,
          name:'滚动字幕',
          type: "marqu",
          active: true,
          draggable:true,
          text: "请输入默认字幕",
          direction:'left',
          duration:'10',
          position: {
            top: 204,
            left: 180,
            height: 24,
            width: 200,
            transform: 0
          },
          style: {
            color: "#333333",
            backgroundColor:'#ffffff',
            fontFamily: "Microsoft Yahei",
            fontSize: "16px",
            fontStyle: "normal",
            fontWeight: "normal",
            lineHeight: 1,
            textAlign: "center",
            textDecoration: "none",
          },
          animation:{
            name:'',
            duration:10,
            delay:1
          }
        }
        break;
      case "time":
        comp={
          compid: uuid,
          name:'时间显示',
          type: "time",
          active: true,
          draggable:true,
          outformat:'yyyy年MM月dd日 HH:mm:ss',
          position: {
            top: 200,
            left: 100,
            width: 200,
            height: 24,
            transform: 0
          },
          style: {
            color: "#333333",
            backgroundColor:'#ffffff',
            fontFamily: "Microsoft Yahei",
            fontSize: "16px",
            fontStyle: "normal",
            fontWeight: "normal",
            lineHeight: 1,
            textAlign: "center",
            textDecoration: "none",
          },
          animation:{
            name:'',
            duration:10,
            delay:1
          }
        }
        break;
      case "video":
        comp={
          compid:uuid,
          name:'视频播放',
          type: 'video',
          active: true,
          draggable:true,
          videolist: [
            {
              name:'默认视频',src:'https://terryz.gitee.io/video/movie.mp4',type:'video/mp4'
            }
          ],
          position: {
            top: 20,
            left: 20,
            width: 320,
            height: 180,
            transform: 0
          },
          animation:{
            name:'',
            duration:10,
            delay:1
          }
        }
        break;
      case "image":
        comp={
          compid:uuid,
          name:'图片播放',
          type: 'image',
          active: true,
          draggable:true,
          imagelist: [
            {name:'默认图片',src:'https://www.baidu.com/img/bd_logo1.png',type:'image/jpeg'}
          ],
          position: {
            top: 20,
            left: 20,
            width: 320,
            height: 180,
            transform: 0
          },
          animation:{
            name:'',
            duration:10,
            delay:1
          }
        }
        break;
      case "music":
        comp={
          compid:uuid,
          name:'背景音乐',
          type: 'music',
          active: true,
          draggable:true,
          musiclist: [
            {name:'默认音乐',src:'http://127.0.0.1:8081/MatStores/20190912/Jewel1.mp3',type:'audio/mp3'}
          ],
          position: {
            top: 0,
            left: 0,
            width: 30,
            height: 30,
            transform: 0
          },
          animation:{
            name:'',
            duration:10,
            delay:1
          }
        }
        break;
      default:break;
    }

    if(comp!=null){
      comps.push(comp)
      state.currentCompId=uuid
    }
  },
  updateComp(state,obj){},
  deleteComp(state,compid){

    let comps=state.slide.pages.find(item=>item.pageid===state.currentPageId).comps;
    let comp=comps.find(item => item.compid === compid)

    if(comp!=null){
      let index = comps.indexOf(comp)
      comps.splice(index, 1)
    }
  },
}
const actions = {
  setAppCode(context, appCode) {
    context.commit('putAppCode', appCode)
  },
  setMySlide(context, slide) {
    context.commit('putMySlide', slide)
  },
  setActiveSlide(context, slideid) {
    context.commit('putActiveSlide', slideid)
  },
  setActivePage(context, pageid) {
    context.commit('putActivePage', pageid)
  },
  setActiveComp(context, compid) {
    context.commit('putActiveComp', compid)
  },
  setResetComps(context) {
    context.commit('putResetComps')
  },
  setActiveCompSize(context,obj){
    context.commit('putActiveCompSize', obj)
  },
  setActiveCompPost(context,obj){
    context.commit('putActiveCompPost', obj)
  },
  addNewPageByDef(context){
    context.commit('addNewPage')
  },
  updatePageByObj(context,obj){
    context.commit('updatePage',obj)
  },
  deletePageById(context,pageid){

    if(pageid!==1) {
      context.commit('deletePage', pageid)
      context.commit('putActivePage', 1)
    }
  },
  addNewCompByType(context,compType){

    context.commit('putResetComps')
    context.commit('addNewComp', compType)
  },
  updateCompByObj(context,obj){
    context.commit('updateComp',obj)
  },
  deleteCompById(context,compid){

    context.commit('deleteComp', compid)

    let comps=state.slide.pages.find(item=>item.pageid===state.currentPageId).comps;
    let defComp=comps.find(item => item.type=== "canvas")
    if(defComp!=null){

      context.commit('putResetComps')
      context.commit('putActiveComp',defComp.compid)
    }
  }
}

const store = new Vuex.Store({
  state,
  getters,
  mutations,
  actions,
 // plugins: [vuexLocal.plugin]
})
export default store



