<template>
  <div class="compbox">
    <mydrag
      :active="getThisComp.active"
      :enable="getThisComp.draggable"
      :handles="'ne, se, sw, nw'"
      :top="getThisComp.position.top"
      :left="getThisComp.position.left"
      :width="getThisComp.position.width"
      :height="getThisComp.position.height"
      :transform="getThisComp.position.transform"
    >
      <swiper class="inner" :options="swiperOption" ref="mySwiper" >
        <swiper-slide v-for="item in getThisComp.imagelist" :key="item.name">
          <img :src="item.src" width="100%" height="100%">
        </swiper-slide>
      </swiper>
    </mydrag>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import mydrag from '@/plugin/mydrag'

  export default {
    components: {
      mydrag
    },
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data: function () {
      return {
        swiperOption: {
          autoplay:{
            delay:2500
          }
        }
      }
    },
    mounted() {
      // current swiper instance
      // 然后你就可以使用当前上下文内的swiper对象去做你想做的事了
      //wkshop.log('this is current swiper instance object', this.swiper)
      this.swiper.allowTouchMove= false;//设置
    },
    computed: {
      ...mapGetters([
        'getCompById'
      ]),
      getThisComp:function(){
        return this.getCompById(this.compid)
      },
      animateStyle: function () {
        return {
          animationName: this.getThisComp.name,
          animationDuration: this.getThisComp.duration + 's',
          animationDelay: this.getThisComp.delay + 's'
        };
      },
      swiper() {
        return this.$refs.mySwiper.swiper
      }
    }
  }
</script>
<style scoped>
  .compbox {
    position: absolute;
    top: 0;
    left: 0;
  }

  .inner {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



