<template>
  <el-tabs type="border-card">
    <el-tab-pane label="组件" class="wraptab">
      <div class="wrapbtn">&nbsp;
        <el-button type="primary" class="btnbox" v-for="comp in compList" v-bind:key="comp.id" v-on:click.stop="addNewComp(comp.type)">{{comp.name}}</el-button>
      </div>
    </el-tab-pane>
    <el-tab-pane label="图层" class="wraptab">
      <ul>
        <li  v-for="comp in getPageComps" v-bind:key="comp.compid" :class="{ 'active': comp.active}" @click="activeComp(comp.compid)">
          {{comp.name}}&nbsp;&nbsp;
          <el-button size="mini" type="danger" plain @click.stop="deleteComp(comp.compid)" v-show="comp.type!=='canvas'">删</el-button>
        </li>
      </ul>
    </el-tab-pane>
  </el-tabs>
</template>
<script>

  import { mapGetters } from 'vuex'
  import { mapActions } from 'vuex'

  export default {
    data() {
      return {
        compList:[
          {
            id:1,
            name:'文本',
            type:'text',
            tip:'文本',
            icon:'',
            remark:''
          },
          {
            id:2,
            name:'字幕',
            type:'marqu',
            tip:'字幕',
            icon:'',
            remark:''
          },
          {
            id:3,
            name:'图片',
            type:'image',
            tip:'图片',
            icon:'',
            remark:''
          },
          {
            id:4,
            name:'视频',
            type:'video',
            tip:'视频',
            icon:'',
            remark:''
          } ,
          {
            id:5,
            name:'时间',
            type:'time',
            tip:'时间',
            icon:'',
            remark:''
          },
          {
            id:6,
            name:'音乐',
            type:'music',
            tip:'音乐',
            icon:'',
            remark:''
          }
        ]
      };
    },
    computed: {
      ...mapGetters([
        'getCurrentPage', 'getAllComps'
      ]),
      getThisPage: function () {
        return this.getCurrentPage
      },
      getPageComps: function () {
        return this.getAllComps
      },
    },
    methods: {
      ...mapActions([
        'setActivePage',
        'setActiveComp',
        'setResetComps',
        'addNewCompByType',
        'deleteCompById'
      ]),
      addNewComp:function(compType){
        this.addNewCompByType(compType)
      },
      deleteComp(compid){
        this.deleteCompById(compid)
      },
      activeComp(compid){
        this.setResetComps()
        this.setActiveComp(compid)
      }
    }
  };
</script>
<style scoped>
  .wraptab {
    margin: 0;
    padding: 0;
    min-height: 400px;
  }
  .wrapbtn{
    text-align: left;
  }
  .btnbox{
    margin-top: 20px;
  }

  ul{
    list-style: none;
    margin: 0;
    padding: 0;
    overflow-x: hidden; /*隐藏ul x轴的滚动条*/
  }
  li{
    background: #fdfdfd;
    border-bottom: 1px solid #e1e1e1;
    line-height: 40px;
    padding-left: 10px;
    display: block;
  }
  .active{
    background: yellow;
  }
  li:hover{
    background: yellow;
  }
  </style>



