<template>
  <div class="logobox">
    <img :src="mylogo" width="50" height="50">
  </div>
</template>

<script>
  export default {
    name: 'mylogor',
    data () {
      return {
        mylogo: require('@/assets/logo.png')
      }
    }
  }
</script>
<style scoped>
  .logobox {
    text-align: left;
  }
</style>



