<template>
  <div class="settingbox">
    <el-select v-model="tempValue" placeholder="请选择模板" size="small">
      <el-option
        v-for="item in tempList"
        :key="item.programId"
        :label="item.programName"
        :value="item.programId">
      </el-option>
    </el-select>
    <el-button type="info" size="small" @click="onTemplate()">应用模板</el-button>
    <el-button type="success" size="small" @click="onDebugit()" >调试</el-button>
    <el-button type="success" size="small" @click="onSaveit()">保存</el-button>
    <el-button type="danger" size="small" @click="onQuitit()">退出</el-button>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import { mapActions } from 'vuex'

  export default {
    data() {
      return {
        tempList: [],
        tempValue:''
      };
    },
    mounted(){

      let that = this
      this.$http.get('/IShower/getAllTemplates', {}, function (data) {

        if (data != null && data != '') {
          that.tempList = JSON.parse(data);
        }

      }, function (err) {
        console.log(err)
      })
    },
    computed: {
      ...mapGetters([
        'getCurrentSlide',
        'getCurrentSlideId'
      ]),
    },
    methods: {
      ...mapActions([
        'setMySlide'
      ]),
      onTemplate:function(){

        if(this.tempValue===''){
          this.$dlg.toast('请先选择模板!', {
            messageType: 'warning',
            closeTime: 3 //auto close dialog time(second)
          });
        }
        else {
          let that = this
          var params = {
            id: this.tempValue
          }

          this.$http.get('/IShower/getProgramInfoById', params, function (data) {

            if (data != null && data != '') {
              console.log(data)

              //that.setMySlide(JSON.parse(data.pcontent))
            }

          }, function (err) {
            console.log(err)
          })
        }
      },
      onPreview:function () {
        const { href } = this.$router.resolve({
          name: "Preview",
          params: {
            id:this.tempValue
          }
        });
        window.open(href, '_blank');
      },
      onDebugit:function(){

        const { href } = this.$router.resolve({
          name: "Debug",
          params: {
            id:this.tempValue
          }
        });
        window.open(href, '_blank');
      },
      onSaveit:function () {

        let content=JSON.stringify(this.getCurrentSlide);

        let that = this
        var params = {
          programid:this.getCurrentSlideId,
          pcontent:'content'
        }

        this.$http.post('/IShower/postProgramInfo', params, function (data) {

          if (data != null && data === 'true') {

            that.$dlg.toast('节目保存成功!', {
              messageType: 'success',
              closeTime: 3 //auto close dialog time(second)
            });
          }
          else{

            that.$dlg.toast('节目保存失败!', {
              messageType: 'error',
              closeTime: 3 //auto close dialog time(second)
            });
          }

        }, function (err) {
          console.log(err)
        })
      },
      onQuitit:function () {
      }
    }
  };
</script>

<style scoped>
  .settingbox {
    margin: 0;
    padding: 15px 20px;
    text-align: right;
  }

</style>



