<template>
  <div class="compbox">
    <mydrag
      :active="getThisComp.active"
      :enable="getThisComp.draggable"
      :handles="'ne, se, sw, nw'"
      :top="getThisComp.position.top"
      :left="getThisComp.position.left"
      :width="getThisComp.position.width"
      :height="getThisComp.position.height"
      :transform="getThisComp.position.transform"
    >
      <video-player  class="inner video-player vjs-custom-skin"
                     ref="videoPlayer"
                     :options="playerOptions"
                     :playsinline="true"
                     @ended="onPlayerEnded($event)">
      </video-player>
    </mydrag>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import mydrag from '@/plugin/mydrag'

  export default {
    components: {
      mydrag
    },
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data: function () {
      return {
        playindex:0,
        playerOptions: {
          // videojs options
          autoplay: true,
          loop: false,
          language: 'en',
          preload: 'auto',
          playbackRates: [0.7, 1.0, 1.5, 2.0],
          fluid: true,
          sources: [],
          poster: require('@/assets/video.png')
        }
      }
    },
    mounted(){
      this.playerOptions.sources=this.getThisComp.videolist
    },
    computed: {
      ...mapGetters([
        'getCompById'
      ]),
      getThisComp:function(){
        return this.getCompById(this.compid)
      },
      animateStyle: function () {
        return {
          animationName: this.getThisComp.name,
          animationDuration: this.getThisComp.duration + 's',
          animationDelay: this.getThisComp.delay + 's'
        };
      },
      getPlayer() {
        return this.$refs.videoPlayer.player
      },
      currVideo() {
        return this.getThisComp.videolist[this.playindex].src
      }
    },
    methods:{
      // listen event

      onPlayerEnded(player) {

        if (this.getThisComp.videolist.length > 1) {
          if (this.playindex < (this.getThisComp.videolist.length - 1)) {
            this.playindex = this.playindex + 1
          } else {
            this.playindex = 0
          }

          this.getPlayer.src(this.getThisComp.videolist[this.playindex].src)
        } else {
          this.getPlayer.play()
        }
      }
    }
  }
</script>
<style scoped>
  .compbox {
    position: absolute;
    top: 0;
    left: 0;
  }

  .inner {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



