// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
import vDialogs from 'v-dialogs'
import VueVideoPlayer from 'vue-video-player'
import 'video.js/dist/video-js.css'
import VueAwesomeSwiper from 'vue-awesome-swiper'
import 'swiper/dist/css/swiper.css'
import App from './App'
import router from './router'
import store from './stores'
import http from './myutils/httputil'
import './myutils/dateutil'
//import pubs from './myutils/pubsutil'

// 需要注意的是，jquery-ui引入的时候，
// 直接写 import juqery-ui 没有效果，只能直接写到具体的方法
// 好处是需要引用的也只有两个  draggable  droppable
import $ from 'jquery'
import '../static/jqueryui/themes/smoothness/jquery-ui.min.css' /*引入jqueryui公共样式*/
import 'jquery-ui/ui/widgets/draggable'
import 'jquery-ui/ui/widgets/droppable'
import 'jquery-ui/ui/widgets/resizable'

Vue.use(ElementUI)
Vue.use(vDialogs)
Vue.use(VueVideoPlayer, /* {
  options: global default options,
  events: global videojs events
} */)
Vue.use(VueAwesomeSwiper, /* { default global options } */)

Vue.prototype.$http = http
//Vue.prototype.$pubs = pubs
Vue.config.productionTip = false

new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



