<template>
  <div class="compbox">
    <mydrag
      :active="getThisComp.active"
      :enable="getThisComp.draggable"
      :handles="'ne, se, sw, nw'"
      :top="getThisComp.position.top"
      :left="getThisComp.position.left"
      :width="getThisComp.position.width"
      :height="getThisComp.position.height"
      :transform="getThisComp.position.transform"
    >
      <div class="inner" :style="[getThisComp.style, animateStyle]">
        <span>{{nowtime}}</span>
      </div>
    </mydrag>
  </div>
</template>
<script>

  import { mapGetters } from 'vuex'
  import mydrag from '@/plugin/mydrag'

  export default {
    components: {
      mydrag
    },
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data: function () {
      return {
        nowtime:(new Date()).Format('yyyy年MM月dd日 HH:mm:ss')
      }
    },
    mounted() {
      let that = this; // 声明一个变量指向Vue实例this，保证作用域一致
      this.timer = setInterval(() => {
        that.nowtime = (new Date()).Format(that.getThisComp.outformat); // 修改数据date
      }, 1000)
    },
    beforeDestroy() {
      if (this.timer) {
        clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
      }
    },
    computed: {
      ...mapGetters([
        'getCompById'
      ]),
      getThisComp:function(){
        return this.getCompById(this.compid)
      },
      animateStyle: function () {
        return {
          animationName: this.getThisComp.name,
          animationDuration: this.getThisComp.duration + 's',
          animationDelay: this.getThisComp.delay + 's'
        };
      }
    }
  }
</script>
<style scoped>
  .compbox {
    position: absolute;
    top: 0;
    left: 0;
  }

  .inner {
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
</style>



