<template>
  <div class="wrapbox">
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="4"><mylogor></mylogor></el-col>
          <el-col :span="2">&nbsp;</el-col>
          <el-col :span="18"><setting></setting></el-col>
        </el-row>
      </el-header>
      <el-container>
        <el-aside width="200px" >
          <el-scrollbar>
            <xtabars></xtabars>
          </el-scrollbar>
        </el-aside>
        <el-main id="maindiv" :style="{height:(screenHeight-80)+'px'}">
          <el-scrollbar>
            <xworks></xworks>
          </el-scrollbar>
        </el-main>
        <el-aside width="400px" >
          <el-scrollbar>
            <xprops></xprops>
          </el-scrollbar>
        </el-aside>
      </el-container>
    </el-container>
  </div>
</template>

<script>

  import { mapGetters } from 'vuex'
  import { mapActions } from 'vuex'

  import mylogor from '@/frames/mylogor'
  import xmenu from '@/zones/xmenu'
  import setting from '@/zones/setting'
  import xtabars from '@/zones/xtabars'
  import xworks from '@/zones/xworks'
  import xprops from '@/zones/xprops'

  export default {
    components: {
      mylogor,
      xmenu,
      setting,
      xtabars,
      xworks,
      xprops,
    },
    data () {
      return {
        // screenWidth: document.documentElement.clientWidth, // 屏幕宽度
        screenHeight: document.documentElement.clientHeight // 屏幕高度
      }
    },
  mounted () {

    //设置当前主页面
    this.setActivePage(this.getHomePageId)
    /*
    let slideId=this.$http.getUrlKey("id");
    this.setActiveSlide(slideId);
    */

    var that = this
    window.onresize = function () { // 定义窗口大小变更通知事件
      // that.screenWidth = document.documentElement.clientWidth // 窗口宽度
      that.screenHeight = document.documentElement.clientHeight // 窗口高度
    }
  },
    watch: {
      // 'screenWidth': function (val) { // 监听屏幕宽度变化
      //   var oIframe = document.getElementById('maindiv')
      //   oIframe.style.width = (Number(val)) + 'px' // '120'是页面布局调整，可去除
      // },
      'screenHeight': function (val) { // 监听屏幕高度变化
        var oIframe = document.getElementById('maindiv')
        // alert(this.$store.getters.screenHeight)
        oIframe.style.height = (Number(val) - 10) + 'px'
      }
    },
    methods: {
      ...mapActions([
        'setActivePage',
        'setActiveSlide'
      ]),
    },
    computed: {
      ...mapGetters([
        'getHomePageId'
      ]),
    }
  }
</script>

<style>
  .wrapbox {
    margin: 0;
    padding: 0;
  }

  .el-header, .el-footer {
    background-color: #409EFF;
    color: #fefefe;
  }

  .el-main {
    background-color: #909399;
    color: #303133;
    border:solid #909399 1px;
  }

  .el-aside{
    background-color: #E4E7ED;
    color: #606266;
  }

  .el-scrollbar {
    height: 100%;
    width: 100%;
  }
  .el-scrollbar__wrap {
    overflow: scroll;
    overflow-x: hidden;
  }

</style>



