<template>
  <div class="wrapcolor">
    <div class="line"></div>
    <el-container>
      <el-header >
        <el-menu :default-active="menuIndex" mode="horizontal" @select="menuSelect" >
          <el-menu-item :index="nav.classId" v-for="nav in navigList" :key="nav.classId">{{nav.className}}</el-menu-item>
        </el-menu>
      </el-header>
      <el-main>
        <el-checkbox-group v-model="checkList" :fill="fillcolor">
        <ul class="img-list">
          <li v-for="item in materList">
            <img :src="item.matPoster" >
              <el-checkbox :label="item.matId">{{item.matName}}</el-checkbox>
          </li>
        </ul>
        </el-checkbox-group>
        <el-pagination
          :current-page="pageIndex"
          :page-size="pageSize"
          layout="total, prev, pager, next"
          :total="matTotal"
          @current-change="pageChange"
        >
        </el-pagination>
      </el-main>
      <el-footer>
        <el-button type="primary" @click="confirm()">确定选择</el-button>
        <el-button type="info" @click="cancel()">关闭窗口</el-button>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: 'matedlg',
    data () {
      return {
        fillcolor:'red',
        menuIndex: '1',
        pageIndex:1,
        pageSize:16,
        pageCondt:'',
        matTotal:1,
        navigList:[
          {classId:'1',className:'所有素材'}
        ],
        checkList:[],
        materList: [
          {
            matId:1,
            matName:'pic1',
            matPoster:'https://www.baidu.com/img/bd_logo1.png',
            matType:'',
            matUrl:'https://www.baidu.com/img/bd_logo1.png',
            matSize:'',
            mDuration:''
        }],
      }
    },
    mounted(){

      let that = this
      this.$http.get('/IShower/getAllMateClasses', {}, function (data) {

        if (data != null && data != '') {
          that.navigList = JSON.parse(data);
        }

      }, function (err) {
        console.log(err)
      })

      this.pageCondt=''
      this.getMatTotal(this.pageCondt)
      this.getMaterials(this.pageIndex,this.pageSize,this.pageCondt)
    },
    methods:{
      getMatTotal(condt){

        let that = this
        var params = {
          condition:condt
        }

        this.$http.get('/IShower/getMatInfosTotal', params, function (data) {

          if (data != null && data != '') {
            that.matTotal = parseInt(data);
          }

        }, function (err) {
          console.log(err)
        })
      },
      getMaterials(pindex,psize,condt){

        let that = this

        var params = {
          pageindex:pindex,
          pagesize:psize,
          condition:condt
        }

        this.$http.get('/IShower/getMatInfosByPage', params, function (data) {

          if (data != null && data != '') {
            that.materList = JSON.parse(data);
          }

        }, function (err) {
          console.log(err)
        })
      },
      menuSelect:function(key, keyPath){

        let condt="MClassNo='"+key+"'"
        if(key==='1'){
          condt=''
        }
        this.pageIndex=1
        this.pageCondt=condt
        this.getMatTotal(this.pageCondt)
        this.getMaterials(this.pageIndex,this.pageSize,this.pageCondt)
      },
      pageChange(val) {

        this.getMaterials(val,this.pageSize,this.pageCondt)
      },
      confirm:function () {

        let that=this
        let retlist=[]
        if(this.checkList.length>0){

          this.checkList.forEach(function(matid){
            let mat=that.materList.find(item=>item.matId===matid)
            let retval={name:mat.matName,src:mat.matUrl,type:mat.matType}
            retlist.push(retval)
          })

          this.$emit('close', { itemList: retlist });
        }

      },
      cancel:function () {
        this.$dlg.closeAll()
      }
    }
  }
</script>
<style scoped>
  .wrapcolor {
    margin: 0;
    padding: 0;
  }
  .el-header,.el-menu{
    background-color: #ffffff;
  }
  .el-main {
    background-color: #F2F6FC;
    text-align: center;
    min-height:450px;
    border: 0;
  }
  .el-checkbox{
    margin-top: 15px;
  }
  .el-footer{
    padding-top: 20px;
    text-align: center;
    background-color: #ffffff;
  }
  .line{
    border: 0;
    border-bottom: 1px solid #F2F6FC;
  }

  .img-list {
    list-style:none;
    overflow: hidden;
  }

  .img-list li {
    position: relative;
    float: left;
    width: 105px;
    height: 150px;
    margin-right: 5px;
    margin-bottom: 5px;
    text-align: center;
    border: 1px solid #ccc;
    cursor: pointer;
  }

  .img-list li:hover {
    background-color: #409EFF;
    color:white;
  }

  .img-list li img {
    margin-top: 3px;
    width: 100px;
    height: 100px;
  }
</style>



