<template>
  <el-tabs type="border-card">
    <el-tab-pane label="样式" class="wraptab">
      <el-form  label-width="50px">
        <el-row>
          <el-col :span="12">
            <el-form-item label="上边:" >
              <el-input-number v-model="getCurrentComp.position.top" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="左边:" >
              <el-input-number v-model="getCurrentComp.position.left" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="12">
            <el-form-item label="宽度:">
              <el-input-number v-model="getCurrentComp.position.width" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
          <el-col :span="12">
            <el-form-item label="高度:">
              <el-input-number v-model="getCurrentComp.position.height" controls-position="right" size="small" :min="1" :max="10000"></el-input-number>
            </el-form-item>
          </el-col>
        </el-row>
        <el-row>
          <el-col :span="24">
            <el-card class="box-card">
              <div slot="header" class="clearfix">
                <span>音乐列表</span>
                <el-button style="float: right; padding: 3px 0;margin-left: 20px;" type="text" @click="onDelMusic">删除</el-button>
                <el-button style="float: right; padding: 3px 0" type="text" @click="onAddMusic">添加</el-button>
              </div>
              <div class="text item">
                <el-checkbox-group v-model="checkList">
                  <p class="wrapchkbox" v-for="item in getCurrentComp.musiclist"><el-checkbox :label="item.name"></el-checkbox></p>
                </el-checkbox-group>
              </div>
            </el-card>
          </el-col>
        </el-row>
      </el-form>
    </el-tab-pane>
    <el-tab-pane label="动画" class="wraptab">
      <el-form label-width="50px">
        <el-form-item label="类型:">
          <el-select v-model="getCurrentComp.animation.name" placeholder="请选择">
            <el-option-group
              v-for="group in getAnimtypeList"
              :key="group.label"
              :label="group.label">
              <el-option
                v-for="item in group.options"
                :key="item.value"
                :label="item.label"
                :value="item.value">
              </el-option>
            </el-option-group>
          </el-select>
        </el-form-item>
        <el-form-item label="速度:">
          <el-slider v-model="getCurrentComp.animation.duration" :min="1" :max="100" show-input input-size="small"></el-slider>
        </el-form-item>
        <el-form-item label="延迟:">
          <el-slider v-model="getCurrentComp.animation.delay" :min="0" :max="100" show-input input-size="small"></el-slider>
        </el-form-item>
      </el-form>
    </el-tab-pane>
  </el-tabs>
</template>
<script>

  import { mapGetters } from 'vuex'
  import matedlg from '@/frames/matedlg'

  export default {
    props: {
      compid: {
        type: Number,
        required: true
      }
    },
    data() {
      return {
        checkList:[]
      }
    },
    computed: {
      ...mapGetters([
        'getCurrentComp',
        'getAnimtypeList'
      ]),
    },
    methods: {
      onAddMusic:function () {

        let that=this
        this.$dlg.modal(matedlg, {
          title:'选择素材',
          width: 1050,
          height: 650,
          params: {},
          callback: data => {
            if(data!=null) {

              data.itemList.forEach(function(obj){

                let mat=that.getCurrentComp.musiclist.find(item=>item.name===obj.name)
                if(mat==null) {
                  that.getCurrentComp.musiclist.push(obj)
                }
              })
            }
          }
        })
      },
      onDelMusic:function () {

        let that=this
        this.checkList.forEach(function (matname) {

          let mat=that.getCurrentComp.musiclist.find(item=>item.name===matname)
          if(mat!=null){
            let index = that.getCurrentComp.musiclist.indexOf(mat)
            that.getCurrentComp.musiclist.splice(index, 1)
          }
        })
      }
    }
  };
</script>
<style scoped>
  .wraptab {
    margin: 0;
    padding: 0;
    min-height: 400px;
  }

  .wrapchkbox{
    padding-bottom: 15px;
  }
</style>



